import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {ConfigService} from '../config.service';
import {AuthService, AuthStatus} from "../auth/auth.service";
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import {Observable} from "rxjs/Observable";

@Injectable()
export class UserService {
  public authStatus: AuthStatus;
  public user = new BehaviorSubject([]);

  constructor(private authService: AuthService,
              private httpClient: HttpClient,
              private configService: ConfigService) {
    this.authService.getAuthStatus().subscribe(
      res => {
        this.authStatus = res;
        this.onInit();
      }
    );
  }

  onInit() {
    this.getUserIfno();
  }

  public getUserIfno() {
    this.httpClient
      .post<any[]>(`${this.configService.url_base}/account/getInfo`,
        `access_token=${this.authStatus.access_token}`, {
          headers: {
            'content': 'application/json',
            'content-type': 'application/x-www-form-urlencoded'
          }
        }).subscribe(
      res => {
        this.user.next(res);
      },
      err => console.log(err)
    );
  }

  public getUser() {
    return this.user.asObservable();
  }


  public postFile(fileToUpload: File): Observable<any> {
    const formData: FormData = new FormData();
    formData.append('access_token', this.authStatus.access_token);
    formData.append('file', fileToUpload, fileToUpload.name);
    return this.httpClient
      .post<any[]>(`${this.configService.url_base}/account/setPhoto`, formData);
  }

}



