import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {ConfigService} from '../config.service';
import {Router} from "@angular/router";
import {BehaviorSubject} from "rxjs/BehaviorSubject";

@Injectable()
export class AuthService {
  public authStatus;
  public auth = new BehaviorSubject<any>([]);

  constructor(private httpClient: HttpClient,
              private configService: ConfigService,
              private router: Router) {
    this.authStatus = new AuthStatus();
    this.auth.next(this.authStatus);
  }

  public accountAuth(login, passwd) {
    this.httpClient
      .post<any[]>(`${this.configService.url_base}/account/auth`, `passwd=${passwd}&email=${login}`, {
        headers: {
          'content': 'application/json',
          'content-type': 'application/x-www-form-urlencoded'
        }
      }).subscribe(
      res => this.checkAuth(res),
      err => console.log('error',err)
    );
  }

  private checkAuth(res) {
    if (res['access_token'] !== undefined) {
      this.authStatus.setAuth(res['access_token']);
      this.auth.next(this.authStatus);
      this.router.navigate(['user']);
    } else if (res['error_code'] !== undefined) {
      console.log(res['error_msg']);
    }

  }

  public getAuthStatus() {
    return this.auth.asObservable();
  }


}

export class AuthStatus {
  status: boolean;
  access_token: string;

  constructor() {
    if (localStorage.getItem('auth') === null && localStorage.getItem('access_token') === null) {
      localStorage.setItem('auth', 'false')
      localStorage.setItem('access_token', '')
    }

    this.status = localStorage.getItem('auth') === "true";
    this.access_token = localStorage.getItem('access_token');
  }

  setAuth(access_token) {
    localStorage.setItem('auth', 'true')
    localStorage.setItem('access_token', access_token)

    this.status = localStorage.getItem('auth') === "true";
    this.access_token = localStorage.getItem('access_token');
  }

  public desAuth() {
    localStorage.clear();
    this.status = false;
    this.access_token = '';
  }
}





