import {BrowserModule} from '@angular/platform-browser';
import {LOCALE_ID, NgModule} from '@angular/core';

import {RouterModule, Routes, CanActivate} from '@angular/router';
import {HttpClientModule} from '@angular/common/http';
import {ReactiveFormsModule} from '@angular/forms';

import {CollapseModule, BsDropdownModule} from 'ngx-bootstrap';


import localeUk from '@angular/common/locales/uk';
import {registerLocaleData} from '@angular/common';

registerLocaleData(localeUk, 'uk');


import {ConfigService} from './config.service';
import {HospitalService} from './hospital/hospital.service';
import {DoctorService} from './doctor/doctor.service';
import {RegisterService} from './register/register.service';
import {AuthService} from './auth/auth.service';
import {UserService} from './user/user.service'
import {RecordService} from "./record/record.service";
import {MedcardService} from "./medcard/medcard.service";
import {AuthGuard} from './auth/auth.guard';


import {AppComponent} from './app.component';
import {AuthComponent} from './auth/auth.component';
import {RegisterComponent} from './register/register.component';
import {DoctorComponent} from './doctor/doctor.component';
import {HospitalComponent} from './hospital/hospital.component';
import {UserComponent} from './user/user.component';
import {CardComponent as UserCardComponent} from './user/card/card.component';
import {InfoComponent as MedCardInfoComponent} from './medcard/info/info.component';
import {ListComponent} from './record/list/list.component';
import {ComplaintsComponent} from './medcard/complaints/complaints.component';
import {IndexeComponent} from './medcard/indexe/indexe.component';


const appRoutes: Routes = [
  {path: 'auth', component: AuthComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'doctor', component: DoctorComponent},
  {path: 'hospital', component: HospitalComponent},
  {path: 'user', component: UserComponent, canActivate: [AuthGuard]},
  {path: '', redirectTo: '/register', pathMatch: 'full'},
  {path: '**', redirectTo: '/auth'}
];


@NgModule({
  declarations: [
    AppComponent,
    AuthComponent,
    RegisterComponent,
    DoctorComponent,
    HospitalComponent,
    UserComponent,
    UserCardComponent,
    MedCardInfoComponent,
    ListComponent,
    ComplaintsComponent,
    IndexeComponent,
  ],
  imports: [
    CollapseModule.forRoot(),
    BsDropdownModule.forRoot(),
    BrowserModule,
    HttpClientModule,
    ReactiveFormsModule,
    RouterModule.forRoot(
      appRoutes,
      {enableTracing: true} // <-- debugging purposes only
    ),
  ],
  providers: [
    ConfigService,
    HospitalService,
    DoctorService,
    RegisterService,
    AuthService,
    UserService,
    RecordService,
    MedcardService,
    AuthGuard,
    {provide: LOCALE_ID, useValue: 'uk'}
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    UserCardComponent,
    MedCardInfoComponent,
    ListComponent,
    ComplaintsComponent,
    IndexeComponent,],
})
export class AppModule {
}



