export class User {
  public _id: number;
  public _first_name: string;
  public _mid_name: string;
  public _last_name: string;
  public _phone: string;
  public _adress: string;
  public _email: string;
  public _sex: string;
  public _date_of_birth: string;
  public _photo_path: string;
  public _utype: string;


  constructor(id: number, first_name: string, mid_name: string, last_name: string, phone: string,
              adress: string, email: string, sex: string, date_of_birth: string, photo_path: string, utype: string) {
    this._id = id;
    this._first_name = first_name;
    this._mid_name = mid_name;
    this._last_name = last_name;
    this._phone = phone;
    this._adress = adress;
    this._email = email;
    this._sex = sex;
    this._date_of_birth = date_of_birth;
    this._photo_path = photo_path;
    this._utype = utype;
  }
}



