import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {ConfigService} from "../config.service";
import {AuthService, AuthStatus} from "../auth/auth.service";
import {BehaviorSubject} from "rxjs/BehaviorSubject";

@Injectable()
export class RecordService {

  public authStatus: AuthStatus;
  public records = new BehaviorSubject([]);

  constructor(private authService: AuthService,
              private httpClient: HttpClient,
              private configService: ConfigService) {
    this.authService.getAuthStatus().subscribe(
      res => {
        this.authStatus = res;
        this.onInit();
      }
    );
  }

  private onInit() {
    this.getHttpUserRecords();
  }

  private getHttpUserRecords() {
    this.httpClient
      .post<any[]>(`${this.configService.url_base}/record/get`,
        `access_token=${this.authStatus.access_token}`, {
          headers: {
            'content': 'application/json',
            'content-type': 'application/x-www-form-urlencoded'
          }
        }).subscribe(
      res => {
        this.records.next(res['records']);
        console.log(res);
      },
      err => console.log(err)
    );
  }

  public getUserRecords() {
    return this.records.asObservable();
  }
}



