import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs/observable';
import {ConfigService} from '../config.service';
import {AuthService} from "../auth/auth.service";
import {Router} from "@angular/router";

@Injectable()
export class RegisterService  {

  constructor(private httpClient: HttpClient,
              private configService: ConfigService,
              private authService: AuthService,
              private  router: Router) {
  }


  public register(registerFormGroup): Observable<any[]> {

    const [last_name, first_name, mid_name] = registerFormGroup.value['name'].split(' ');
    const params =
      `email=${registerFormGroup.value['email']}&` +
      `date_of_birth=` +
      `${registerFormGroup.value['year']}/${registerFormGroup.value['mon']}/${registerFormGroup.value['day']}&` +
      `last_name=${last_name}&` +
      `mid_name=${mid_name}&` +
      `first_name=${first_name}&` +
      `phone=${registerFormGroup.value['phone']}&` +
      `sex=${registerFormGroup.value['sex']}&` +
      `password=${registerFormGroup.value['passwd']}`;

    return this.httpClient
      .post<any[]>(`${this.configService.url_base}/account/register`, params, {
        headers: {
          'content': 'application/json',
          'content-type': 'application/x-www-form-urlencoded'
        }
      });
  }

}



